import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import "./App.css";

import Header from "./header/Header";
import SearchPage from "./searchpage/SearchPage";
import ShortListPage from "./shortlistpage/ShortListPage";
import CampaignPage from "./campaignpage/CampaignPage";
import CampaignForm from "./campaignpage/campaignform/CampaignForm";
import LoginForm from "./formContainer/loginForm/LoginForm";
import SignupForm from "./formContainer/signupForm/SignupForm";

function App() {
  return (
    <div>
      {/* <Header /> */}
      <Switch>
        <Route path="/lists" component={ShortListPage} />
        <Route path="/campaigns" component={CampaignPage} />
        <Route path="/campaignform" component={CampaignForm} />
        <Route path="/searchpage" component={SearchPage} />
        {/* <PrivateRoute path="/protected" component={Protected} /> */}
        <Route path="/login" component={LoginForm} />
        <Route path="/signup" component={SignupForm} />
        <Route exact path="/" component={SignupForm} />
      </Switch>
    </div>
  );
}

function PrivateRoute({ component: Component, ...rest }) {
  return (
    <Route
      {...rest}
      render={props =>
        props.isAuthenticated ? (
          <Component {...props} />
        ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: props.location }
              }}
            />
          )
      }
    />
  );
}

const mapStateToProps = state => ({
  isAuthenticated: state.isAuthenticated
});

connect(mapStateToProps)(PrivateRoute);

export default App;
