import React, { Component } from 'react';
import { connect } from 'react-redux';

class DownloadReportModal extends Component {
    clickHandler() {
        const userListInfo = {
            userCredentials: this.props.userCredentials,
            listName: this.props.listWhoseReportGenerated
        }
        const url = `https://secure-bastion-20455.herokuapp.com/dashboard/export/csv/${userListInfo.userCredentials}/${userListInfo.listName}.csv`;
        window.open(url);
    }
    render() {
        return (
            <div style={{ "zIndex": 100 }} className="modal fade" id="downloadReport" tabIndex="-1" role="dialog" data-backdrop="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Download the Campaign performance report</p>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-primary"
                                onClick={() => { this.clickHandler() }}
                                data-dismiss="modal"
                            >Download</button>
                            <button
                                type="button"
                                className="btn btn-secondary"
                                data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => ({
    userCredentials: state.userCredentials,
    listWhoseReportGenerated: state.listWhoseReportGenerated
});

export default connect(mapStateToProps)(DownloadReportModal);