import React, { Component } from 'react';
import { connect } from 'react-redux';
import logo from '../../assests/images/logo.png';
import './Header.css';
import DropDownOnClick from '../utilities/dropdownonclick/DropDownOnClick';


class Header extends Component {

    HeaderDropdowncontent = [];

    renderDropDown() {
        if (this.props.userEmailId === "") {
            return null;
        } else {
            this.HeaderDropdowncontent = [
                { value: this.props.userEmailId, id: "searchpage" },
                { value: 'Search Page', id: "searchpage" },
                { value: 'My Lists', id: "lists" },
                { value: 'Log Out', id: "login" },
            ];
        }
        return <DropDownOnClick
            name="page-selected"
            classname="select-box--box "
            belongTo="Header"
            items={this.HeaderDropdowncontent}
        />

    }

    render() {
        return (
            <header>
                <div className="logo">
                    <img src={logo} alt="logo" />
                </div>
                <div id="login" className="dropdown">
                    {this.renderDropDown()}
                </div>
            </header>
        );
    }
}
const mapStateToProps = state => ({
    userEmailId: state.userCredentials
});
export default connect(mapStateToProps)(Header);