function getFormattedURLParameters(parametersString, type) {
  let formattedParameter = `${type}=`;
  let endCharacter = "|";
  if (parametersString) {
    let parametersArray = parametersString.split(",");
    let totalparameters = parametersArray.length - 1;
    for (let i = 0; i < parametersArray.length; i++) {
      if (i === totalparameters) {
        endCharacter = "";
      }
      formattedParameter += `${encodeURIComponent(parametersArray[i])}${endCharacter}`;
    }
  }

  return formattedParameter;
}

function getFormattedEngagement(engagementsString, type) {
  let formattedEngagement = "", maxengagement = "None", minengagement = "None";
  let sortedEngagementArray = [];
  if (engagementsString) {
    let engagementsArray = engagementsString.split(",");
    for (let i = 0; i < engagementsArray.length; i++) {
      engagementsArray[i] = engagementsArray[i].replace("%", "");
      engagementsArray[i] = engagementsArray[i].replace(">", "");
      sortedEngagementArray.push(engagementsArray[i].split("-"));
      sortedEngagementArray = [].concat.apply([], sortedEngagementArray);
      sortedEngagementArray = sortedEngagementArray.slice().sort(function (x, y) {
        return x - y;
      });
      maxengagement = sortedEngagementArray[sortedEngagementArray.length - 1];
      minengagement = sortedEngagementArray[0];
    }
  }
  formattedEngagement = `&min_engagement_${type}=${minengagement}&max_engagement_${type}=${maxengagement}`;
  return formattedEngagement;
}
function getFormattedOrderBy(orderBy) {
  if (orderBy) {
    return `&${orderBy}=1`
  } else {
    return "";
  }
}
const getFilter = () => {

  let cat = document.getElementById("category").value;
  let loc = document.getElementById("location").value;
  let pic_eng = document.getElementById("picture-engagement").value;
  let vid_eng = document.getElementById("video-engagement").value;
  let minfoll = document.getElementById("minval").value;
  let maxfoll = document.getElementById("maxval").value;
  let orderby = document.getElementById("order-by").value;

  maxfoll = maxfoll === "" ? "None" : parseInt(maxfoll);
  minfoll = minfoll === "" ? "None" : parseInt(minfoll);
  const filter = {
    categories: getFormattedURLParameters(cat, "categories"),
    locations: getFormattedURLParameters(loc, "location"),
    picture_engagement: getFormattedEngagement(pic_eng, "picture"),
    video_engagement: getFormattedEngagement(vid_eng, "video"),
    minfollowers: minfoll,
    maxfollowers: maxfoll,
    orderby: getFormattedOrderBy(orderby)
  };
  return filter;

}

export { getFilter };
