import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

import "../loginSignupForm.css";

const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
);

const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};
class SignupForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      userName: null,
      agencyName: null,
      email: null,
      password: null,
      confirmpassword: null,
      phoneNumber: null,
      formErrors: {
        userName: "",
        agencyName: "",
        email: "",
        password: "",
        confirmpassword: "",
        phoneNumber: ""
      }
    };

    this.signUpHandler = this.signUpHandler.bind(this);
  }

  signUpHandler = e => {
    e.preventDefault();
    const headers = {
      "Content-Type": "application/json",
      "X-CSRFToken": "x-csrftoken"
    };
    if (formValid(this.state)) {
      axios
        .post(
          "https://secure-bastion-20455.herokuapp.com/accounts/register",
          {
            userName: this.state.userName,
            agencyName: this.state.agencyName,
            email: this.state.email,
            password: this.state.password,
            confirmpassword: this.state.confirmpassword,
            phoneNumber: this.state.phoneNumber
          },
          { headers: headers }
        )
        .then(response => {
          this.props.history.push("/login");
        });
    } else {
      console.error("FORM INVALID - DISPLAY ERROR MESSAGE");
    }
  };

  passwordConfirmation() {
    let passwordField = document.getElementById("password").value;
    let confirmpasswordField = document.getElementById("confirmpassword").value;
    if (confirmpasswordField === passwordField) {
      return true;
    } else {
      return false;
    }
  }

  phoneNumberValidity() {
    let phoneNumberField = document.getElementById("phoneNumber").value;
    let isValidphoneNumber =
      !isNaN(phoneNumberField) &&
      (phoneNumberField.length === 10 ? true : false);
    return isValidphoneNumber;
  }

  handleChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };

    switch (name) {
      case "userName":
        formErrors.userName = "";
        break;
      case "agencyName":
        formErrors.agencyName = "";
        break;
      case "email":
        formErrors.email = emailRegex.test(value)
          ? ""
          : "invalid email address";
        break;
      case "password":
        formErrors.password =
          value.length < 8 ? "minimum 8 characaters required" : "";
        break;
      case "confirmpassword":
        formErrors.confirmpassword = this.passwordConfirmation()
          ? ""
          : "password doesn't match";
        break;
      case "phoneNumber":
        formErrors.phoneNumber = this.phoneNumberValidity()
          ? ""
          : "enter valid phone number";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value });
  };

  render() {
    const { formErrors } = this.state;

    return (
      <div className="wrapper">
        <div className="form-wrapper">
          <h1>Create Account</h1>
          <form
            onSubmit={this.signUpHandler}
            noValidate
            className="login-signup-form"
          >
            <div className="input-width">
              <input
                className={formErrors.userName.length > 0 ? "error" : null}
                placeholder="Full Name"
                type="text"
                name="userName"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.userName.length > 0 && (
                <span className="errorMessage">{formErrors.userName}</span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.agencyName.length > 0 ? "error" : null}
                placeholder="Brand/Agency Name"
                type="text"
                name="agencyName"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.agencyName.length > 0 && (
                <span className="errorMessage">{formErrors.agencyName}</span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.email.length > 0 ? "error" : null}
                placeholder="Email"
                type="email"
                name="email"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.email.length > 0 && (
                <span className="errorMessage">{formErrors.email}</span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Password"
                type="password"
                name="password"
                id="password"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.password.length > 0 && (
                <span className="errorMessage">{formErrors.password}</span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Confirm Password"
                type="password"
                id="confirmpassword"
                name="confirmpassword"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.confirmpassword.length > 0 && (
                <span className="errorMessage">
                  {formErrors.confirmpassword}
                </span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Enter your Phone Number"
                type="tel"
                name="phoneNumber"
                id="phoneNumber"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.phoneNumber.length > 0 && (
                <span className="errorMessage">{formErrors.phoneNumber}</span>
              )}
            </div>
            <div className="form-lower-section">
              <button
                type="submit"
                className={formValid(this.state) === true ? "" : "disabled"}
                disabled={!formValid(this.state)}
              >
                Create Account
              </button>
              <Link to="/login">
                <small>Already Have an Account?</small>
              </Link>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default SignupForm;
