import React, { Component } from "react";
import { connect } from 'react-redux';
import CampaignChoose from '../../campaignpage/campaignchoose/CampaignChoose';
import CategoryFormatter from '../../utilities/categoryformatter/CategoryFormatter'
import InfluencerModal from '../../searchpage/cardcontainer/influencermodal/InfluencerModal'
import ScoreInfo from '../../searchpage/cardcontainer/scoreinfo/ScoreInfo'
import { deleteInfluencersFromGroups } from '../../../actions'
import './ListCampaignContent.css';
import ConfirmDeletionOneList from "../../shortlistpage/ConfirmDeletionOneList";

class ListCampaignContent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalReach: 0,
            totalPictureEngagement: 0,
            totalVideoEngagement: 0,
            selectedList: ""
        }
    }

    componentDidMount() {
        if (this.props.data) {
            const defaultOpen = this.props.type + "1";
            document.getElementById(defaultOpen).style.display = "block";
        }
    }
    getBigger(x, y) {
        x = parseInt(x);
        y = parseInt(y);
        var z = Math.max(x, y);
        return z;
    }
    checkLength(name, x) {
        if (name.length >= x) {
            return name.substring(0, x) + "..";
        } else {
            return name;
        }
    }
    OpenInfluencerModal(id) {
        let modal = document.getElementById(id);
        if (modal !== null) {
            modal.style.display = "block";
            window.onclick = function (event) {
                if (event.target === modal) {
                    modal.style.display = "none";
                }
            }
        }
    }
    convertCategoriesArrayToString(categoriesArray) {
        if (categoriesArray) {
            let categoriesString = "";
            let endCharacter = "";
            for (let i = 0; i < categoriesArray.length; i++) {
                if (i !== (categoriesArray.length - 1)) {
                    endCharacter = ",";
                } else {
                    endCharacter = "";
                }
                categoriesString += categoriesArray[i].name + endCharacter;
            }
            return categoriesString;
        } else {
            return null;
        }
    }
    getFollowersK(x) {
        let y
        if (x >= 0 && x < 1000) {
            return x
        } else
            if (x >= 1000 && x < 1000000) {
                y = (x / 1000).toFixed(1);
                return y + "K";
            } else
                if (x > 1000000) {
                    y = (x / 1000000).toFixed(1);
                    return y + "M";
                }

    }
    deleteInfluencersFromList(influencer_id, listSelected) {
        const influencersListUserInfo = {
            influencersListInfo: {
                list_names: listSelected,
                influencer_ids: influencer_id,
                action: "DELETE"
            },
            userCredentials: this.props.userCredentials
        }
        this.props.deleteInfluencersFromGroups(influencersListUserInfo);
    }
    // renderListSumUpInfo = (data) => {
    //     if (data !== "") {
    //         let listSumUpcard = data.map((card, index) => {
    //             return (
    //                 <div className="listSumUp  item row">
    //                     {/* <ScoreInfo class="col text-center authen"
    //                         score={"-"}
    //                         heading=" Total Cost" /> */}
    //                     {/* <ScoreInfo class="col text-center authen"
    //                         score={this.getFollowersK(this.state.totalReach)}
    //                         heading="Total Reach" />
    //                     <ScoreInfo class="col text-center authen"
    //                         score={this.getFollowersK(this.state.totalPictureEngagement)}
    //                         heading="Total Picture Engagement" />
    //                     <ScoreInfo class="col text-center authen"
    //                         score={this.getFollowersK(this.state.totalVideoEngagement)}
    //                         heading="Total Video Engagement" /> */}
    //                     {/* <ScoreInfo class="col text-center authen"
    //                         score={"-"}
    //                         heading="Cost per Engagement" /> */}
    //                 </div>
    //             )
    //         })
    //     }
    // }
    renderListContent = (data, listName) => {
        if (data !== "") {
            let listcard = data.map((card, index) => {
                let m_id = "Modal" + card.id;
                return (
                    <React.Fragment key={index}>
                        <InfluencerModal id={m_id} data={card} categories_new={this.convertCategoriesArrayToString(card.categories)} belongTo="List" />
                        <div className="item row">
                            <div className="col-sm-2 img-cont-shortlist text-center">
                                <img src={card.photo} alt="" onClick={() => { this.OpenInfluencerModal(m_id) }} />
                            </div>
                            <div className="col-sm-2 mid">
                                <h5 className="user-name" onClick={() => { this.OpenInfluencerModal(m_id) }}>@{this.checkLength(card.username, 12)}</h5>
                                <h6 className="full-name" onClick={() => { this.OpenInfluencerModal(m_id) }}>{this.checkLength(card.full_name, 12)}</h6>
                                <h6><i className="fas fa-map-marker-alt"></i>&nbsp;{card.location_city === null ? "India" : card.location_city}</h6>
                            </div>
                            <div className="col-sm-2 mid">
                                <CategoryFormatter categories={this.convertCategoriesArrayToString(card.categories)} />
                            </div>
                            <div className="col-sm-5 row mid">
                                <div className="col text-center">
                                    <h5 className="font-weight-bolder">{this.getFollowersK(card.followers)}</h5>
                                    <h6>Followers</h6>
                                </div>
                                <div className="col text-center">
                                    <h5 className="font-weight-bolder">{this.getBigger(card.avg_picture_engagement, card.avg_video_engagement)}%</h5>
                                    <h6>Engagement</h6>
                                </div>
                            </div>
                            <div className="col-sm-1 mid">
                                <button className="btn btn-primary" onClick={() => { this.deleteInfluencersFromList(card.id, listName) }}><span><i className="fas fa-times fa-2x" /></span></button>
                            </div>

                        </div>
                    </React.Fragment>
                )
            })
            return listcard;
        }
    }
    renderCampaignButtons = type => {
        if (type === "List") {
            return (
                <div className="buttons">
                    <button className="contentbutton">Save List</button>
                    <button className="contentbutton" data-toggle="modal" data-target="#campaignOptions"> Assign Campaign</button>
                </div>
            )
        } else {
            return null
        }
    }
    renderList() {
        const { type, id, data, listName } = this.props;
        let list_id = type + id;
        let list_name = null;
        if (id === "new") {
            list_name = "Create New " + type;
        } else {
            list_name = listName;
        }
        let newlistName = listName.replace(/\s/g, "");
        return (
            <React.Fragment>
                <div id={list_id} className="tabcontent">
                    <div className="m-0 main-list-heading">{list_name}<button data-toggle="modal" data-target={"#confirmDeletionFor" + newlistName}>Delete List</button></div>
                    {/* {this.renderListSumUpInfo(data)} */}
                    {this.renderListContent(data, listName)}
                    {this.renderCampaignButtons(type)}
                </div>
                <CampaignChoose listName={listName} />
                <ConfirmDeletionOneList listName={newlistName} actualListName={listName} />
            </React.Fragment>
        )
    }
    render() {

        return (
            this.renderList()
        )
    }
}
const mapStateToProps = state => ({
    userCredentials: state.userCredentials,
});

const mapDispatchToProps = dispatch => {
    return {
        deleteInfluencersFromGroups: influencersListUserInfo => {
            dispatch(deleteInfluencersFromGroups(influencersListUserInfo));
        }
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(ListCampaignContent);