import React, { Component } from 'react';
import { connect } from 'react-redux'

import Header from '../header/Header';
import Loader from '../loader/Loader';
import ListCampaignButton from '../utilities/listcamapignbutton/ListCampaignButton';
import ListCampaignContent from '../utilities/listcampaigncontent/ListCampaignContent';

import { fetchCampaignGroupData } from '../../actions/index';


class CampaignPage extends Component {
    componentDidMount() {
        this.props.fetchCampaignGroupData();
    }

    renderList() {
        if (!this.props.isDataLoading) {
            return (
                <div className="row" id="container">
                    <div className="tab col-sm-3" id="listcont">
                        <button className="topbutton mt-0">Select List</button>
                        {this.props.campaignsGroup.map((key, index) => {
                            return (
                                <ListCampaignButton key={key.No} data={key.No} type="Campaign" />
                            );
                        })}
                        <ListCampaignButton
                            data="new"
                            type="Campaign"
                        />
                    </div>
                    <div className="col-sm-9 main-content" id="list-content">
                        {this.props.campaignsGroup.map(key => {
                            return (
                                <ListCampaignContent
                                    key={key.No}
                                    id={key.No}
                                    data={key.list}
                                    type="Campaign"
                                />
                            );
                        })}
                        <ListCampaignContent id="new" data="" type="Campaign" />
                    </div>
                </div>
            );
        } else {
            return <Loader />;
        }
    }
    render() {
        return (
            <React.Fragment>
                <Header />
                <hr />
                {this.renderList()}
            </React.Fragment>

        );
    }
}

const mapStateToProps = state => ({
    campaignsGroup: state.campaignsGroup.groups,
    isDataLoading: state.isDataLoading
});
const mapDispatchToProps = dispatch => {
    return {
        fetchCampaignGroupData: () => {
            dispatch(fetchCampaignGroupData());
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CampaignPage);