import React, { Component } from 'react';
import { connect } from 'react-redux';

import { setSelectedInfluencersData } from '../../../../actions';

class SelectAllInfluencersButton extends Component {

    selectAllInfluencers = () => {
        let selectedInluencersId = "";
        const allCardCheckboxes = document.getElementsByClassName("influencer-card-checkbox");
        for (let i = 0; i < allCardCheckboxes.length; i++) {
            allCardCheckboxes[i].checked = true;
            let checkboxName = (allCardCheckboxes[i].getAttribute("name")).split("-");
            selectedInluencersId += checkboxName[1] + ",";
        }
        this.props.setSelectedInfluencersData(selectedInluencersId);
    }
    render() {
        return (
            <button className="button-style col" onClick={this.selectAllInfluencers}>Select All</button>
        );
    }
}
const mapDispatchToProps = dispatch => {
    return {
        setSelectedInfluencersData: selectedInfluencersData => {
            dispatch(setSelectedInfluencersData(selectedInfluencersData));
        }
    }
}

export default connect(null, mapDispatchToProps)(SelectAllInfluencersButton);