import React, { Component } from 'react';
import { connect } from 'react-redux';

import { deleteAllInfluencersGroups, storeListNames } from "../../actions";

class ConfirmDeletionModal extends Component {

    deleteAllInfluencersGroups = () => {
        this.props.deleteAllInfluencersGroups(this.props.userCredentials);
        const emptyArray = []
        this.props.storeListNames(emptyArray);
    }
    render() {
        return (
            <div className="modal fade" id="confirmDeletion" tabIndex="-1" role="dialog" data-backdrop="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Are you sure you want to delete all the lists</p>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={this.deleteAllInfluencersGroups}>Yes</button>
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    userCredentials: state.userCredentials
});

const mapDispatchToProps = dispatch => {
    return {
        deleteAllInfluencersGroups: userCredentials => {
            dispatch(deleteAllInfluencersGroups(userCredentials));
        },
        storeListNames: newListName => {
            dispatch(storeListNames(newListName));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmDeletionModal);