import React, { Component } from 'react';
import { connect } from 'react-redux';

import { addInfluencersToGroups, addInfluencersGroup, setSelectedInfluencersData } from "../../../actions";
import ConfirmCreationModal from '../../shortlistpage/confirmcreationmodal/ConfirmCreationModal';
class ConfirmAssignModal extends Component {

    clickHandler(userCredentials, listSelected) {
        window.$("#confirmAssign").modal("toggle");
        if (listSelected.includes("Create New List")) {
            listSelected = listSelected.replace("Create New List", "");
            this.props.addInfluencersGroup(userCredentials);
            return <ConfirmCreationModal />
        }
        if (listSelected !== "") {
            const influencersListUserInfo = {
                influencersListInfo: {
                    list_names: listSelected,
                    influencer_ids: this.props.selectedInfluencersData,
                    action: "ADD"
                },
                userCredentials: userCredentials
            }
            this.props.addInfluencersToGroups(influencersListUserInfo);
        }
        let selectedInluencersId = "";
        const allCardCheckboxes = document.getElementsByClassName("influencer-card-checkbox");
        for (let i = 0; i < allCardCheckboxes.length; i++) {
            if (allCardCheckboxes[i].checked) {
                allCardCheckboxes[i].checked = false;
            }
        }
        this.props.setSelectedInfluencersData(selectedInluencersId);
    }
    render() {
        return (
            <div className="modal fade" id="confirmAssign" tabIndex="-1" role="dialog" data-backdrop="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Are you sure you want to add these influencers to List <strong>{this.props.listSelected}</strong></p>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={() => { this.clickHandler(this.props.userCredentials, this.props.listSelected) }}>Yes</button>
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    selectedInfluencersData: state.selectedInfluencersData,
    userCredentials: state.userCredentials
});

const mapDispatchToProps = dispatch => {
    return {
        addInfluencersToGroups: influencersListUserInfo => {
            dispatch(addInfluencersToGroups(influencersListUserInfo));
        },
        addInfluencersGroup: userCredentials => {
            dispatch(addInfluencersGroup(userCredentials));
        },
        setSelectedInfluencersData: selectedInfluencersData => {
            dispatch(setSelectedInfluencersData(selectedInfluencersData));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmAssignModal);