const generateStatisticsReport = hashtagListInfo => dispatch => {
  console.log(hashtagListInfo);
  dispatch(setIsReportGenerated(false))
  return fetch(`https://secure-bastion-20455.herokuapp.com/dashboard/view_influencer_statistics/${hashtagListInfo.userCredentials}/${hashtagListInfo.listName}`,
    {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ hashtag: hashtagListInfo.hashTag })
    })
    .then(response => response.json())
    .then(json => {
      dispatch(setIsReportGenerated(true))
    })

}

