import React, { Component } from "react";

import axios from "axios";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import "../loginSignupForm.css";
import { setUserCredentials, setIsAuthenticated, fetchInfluencerGroupsData } from "../../../actions";

const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
);

const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};

class LoginForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: null,
      password: null,
      formErrors: {
        email: "",
        password: ""
      }
    };
  }

  handleSubmit = e => {
    e.preventDefault();
    const headers = {
      "Content-Type": "application/json",
      "X-CSRFToken": "x-csrftoken"
    };
    if (formValid(this.state)) {
      axios
        .post(
          "https://secure-bastion-20455.herokuapp.com/accounts/login",
          {
            email: this.state.email,
            password: this.state.password
          },
          { headers: headers }
        )
        .then(response => {
          if (response.request.status === 200) {
            this.props.setIsAuthenticated(true);
            this.props.setUserCredentials(response.data.user_id);
            this.props.fetchInfluencerGroupsData(response.data.user_id);
          }
        });
    } else {
      console.error("FORM INVALID - DISPLAY ERROR MESSAGE");
    }
  };

  handleChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };

    switch (name) {
      case "email":
        formErrors.email = emailRegex.test(value)
          ? ""
          : "invalid email address";
        break;
      case "password":
        formErrors.password = value.length < 8 ? "minimum 8 characters" : "";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value });
  };

  render() {
    const { formErrors } = this.state;
    const { isAuthenticated } = this.props;
    return isAuthenticated ? (
      <Redirect
        to={{
          pathname: "/searchpage",
          state: { from: this.props.location }
        }}
      />
    ) : (
        <div className="wrapper">
          <div className="form-wrapper">
            <h1>Log In</h1>
            <form
              onSubmit={this.handleSubmit}
              noValidate
              className="login-signup-form"
            >
              <div className="input-width">
                <input
                  className={formErrors.email.length > 0 ? "error" : null}
                  placeholder="Email"
                  type="email"
                  name="email"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.email.length > 0 && (
                  <span className="errorMessage">{formErrors.email}</span>
                )}
              </div>
              <div className="input-width">
                <input
                  className={formErrors.password.length > 0 ? "error" : null}
                  placeholder="Password"
                  type="password"
                  name="password"
                  id="password"
                  noValidate
                  onChange={this.handleChange}
                />
                {formErrors.password.length > 0 && (
                  <span className="errorMessage">{formErrors.password}</span>
                )}
              </div>
              <div className="form-lower-section">
                <button
                  type="submit"
                  className={formValid(this.state) === true ? "" : "disabled"}
                  disabled={!formValid(this.state)}
                >
                  Log in
              </button>
                <Link to="/signup">
                  <small>New Here ? Create Account</small>
                </Link>
              </div>
            </form>
          </div>
        </div>
      );
  }
}

const mapStateToProps = state => ({
  isAuthenticated: state.isAuthenticated
});

const mapDispatchToProps = dispatch => {
  return {
    setUserCredentials: userCredentials => {
      dispatch(setUserCredentials(userCredentials));
    },
    setIsAuthenticated: isAuthenticated => {
      dispatch(setIsAuthenticated(isAuthenticated));
    },
    fetchInfluencerGroupsData: userCredentials => {
      dispatch(fetchInfluencerGroupsData(userCredentials));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginForm);
