import React, { Component } from 'react';

import ConfirmAssignModal from '../ConfirmAssignModal';

class AssignToListButton extends Component {

    state = {
        selectedListName: ""
    }

    clickHandler() {
        const listSelected = document.getElementById("assignLists").value;
        this.setState(() => ({
            selectedListName: listSelected
        }));
    }

    renderButton = () => {
        if (this.props.userCredentials === "") {
            return null;
        } else {
            return <button
                className="button-style col"
                onClick={() => { this.clickHandler() }}
                data-toggle="modal"
                data-target="#confirmAssign">Assign</button>
        }
    }
    render() {
        return <React.Fragment>
            {this.renderButton()}
            <ConfirmAssignModal listSelected={this.state.selectedListName} />
        </React.Fragment>

    }
}


export default AssignToListButton;