import React, { Component } from 'react';
import './OrderBy.css';
import DropDownOnClick from '../../utilities/dropdownonclick/DropDownOnClick';

class OrderBy extends Component {
    render() {
        return (
            <DropDownOnClick
                name="order-selected"
                belongTo="order-by"
                classname="order-by-select"
                id="order-by"
                items={[
                    { value: 'OrderBy', id: "none" },
                    { value: 'Followers Low to High', id: "followers_ascending" },
                    { value: 'Followers High to Low', id: "followers_descending" },
                    { value: 'Picture Engagement High to Low', id: "pic_engagement_descending" },
                    { value: 'Picture Engagement Low to High', id: "pic_engagement_ascending" },
                    { value: 'Video Engagement High to Low', id: "vid_engagement_descending" },
                    { value: 'Video Engagement Low to High', id: "vid_engagement_ascending" },
                ]}
            />
        );
    }
}
export default OrderBy;