import React, { Component } from "react";

import './ListCampaignButton.css';

import ConfirmDeletionModal from "../../shortlistpage/ConfirmDeletionModal";
import ConfirmCreationModal from '../../shortlistpage/confirmcreationmodal/ConfirmCreationModal';
import MeasurePerformance from '../../shortlistpage/measureperformanceModal/MeasurePerformanceModal'

class ListCampaignButton extends Component {

    openList(type, id) {
        let i, tablinks, tabcontent;
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        let tabName = type + id;
        let button = "button-" + type + id;
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }
        document.getElementById(tabName).style.display = "block";
        document.getElementById(button).className += " active";
    }


    renderButton() {
        const { data, type, ListName } = this.props;
        var b_id = "button-" + type + data;
        let button_name = null;
        if (data === "new") {
            button_name = "Create New " + type;
        } else if (data === "deleteAll") {
            button_name = "Delete All " + type;
        } else if (data === "performance") {
            button_name = "Measure Performance";
        }
        else {
            button_name = ListName;
        }
        let classname = ["tablinks", "tabbutton"];
        if (this.props.data === 1) {
            classname.push("active");
        }
        if (data === "deleteAll") {
            return <button
                className={classname.join(' ')}
                id={b_id}
                data-toggle="modal"
                data-target="#confirmDeletion">{button_name}</button>
        } if (data === "new") {
            return <button
                className={classname.join(' ')}
                id={b_id}
                data-toggle="modal"
                data-target="#confirmCreation">{button_name}</button>
        } if (data === "performance") {
            return <button
                className={classname.join(' ')}
                id={b_id}
                data-toggle="modal"
                data-target="#measurePerformance">{button_name}</button>
        }
        else {
            return (
                <button
                    className={classname.join(' ')}
                    onClick={() => { this.openList(this.props.type, this.props.data) }}
                    id={b_id}
                >{button_name}</button>
            )
        }

    }
    render() {
        return (
            <React.Fragment>
                {this.renderButton()}
                <ConfirmDeletionModal />
                <ConfirmCreationModal listName={this.props.ListName} />
                <MeasurePerformance />
            </React.Fragment>
        )
    }

}

export default ListCampaignButton;