import React, { Component } from 'react';

import './CampaignForm.css';

import Header from '../../header/Header';

class CampaignForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            campaignName: null,
            hashtag: null,
            brief: null,
            formErrors: {
                campaignName: "",
                hashtag: "",
                brief: ""
            }
        }
    }
    render() {
        const { formErrors } = this.state;
        return (
            <React.Fragment>
                <Header />
                <div className="wrapper">
                    <div className="campaign-form-wrapper">
                        <h1>Brief Form</h1>
                        <form
                            onSubmit={this.signUpHandler}
                            noValidate
                            className="campaign-form"
                        >
                            <div className="field-width-label ">
                                <label >Campaign Name</label>
                            </div>

                            <div className="field-width">
                                <input
                                    className={formErrors.campaignName.length > 0 ? "error" : null}
                                    placeholder="Campaign Name"
                                    type="text"
                                    name="campaignName"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.campaignName.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="field-width-label">
                                <label>HashTags</label>
                            </div>
                            <div className="field-width">
                                <input
                                    className={formErrors.hashtag.length > 0 ? "error" : null}
                                    placeholder="Hashtags"
                                    type="text"
                                    name="hashtag"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.hashtag.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="field-width-label">
                                <label>Brief</label>
                            </div>
                            <div className="field-width">
                                <input
                                    className={formErrors.brief.length > 0 ? "error" : null}
                                    placeholder="Brief"
                                    type="text"
                                    name="brief"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.brief.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="field-width-label">
                                <label>Deliverables</label>
                            </div>
                            <div className="field-width">
                                <input
                                    className={formErrors.brief.length > 0 ? "error" : null}
                                    placeholder="Deliverables"
                                    type="text"
                                    name="deliverbles"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.brief.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="field-width-label">
                                <label>Commerical Range</label>
                            </div>
                            <div className="field-width">
                                <input
                                    className={formErrors.brief.length > 0 ? "error" : null}
                                    placeholder="Commericals"
                                    type="text"
                                    name="range"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.brief.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="field-width-label">
                                <label>Content Live Date</label>
                            </div>
                            <div className="field-width">
                                <input
                                    className={formErrors.brief.length > 0 ? "error" : null}
                                    placeholder="live-date"
                                    type="date"
                                    name="brief"
                                    noValidate
                                    onChange={this.handleChange}
                                />
                                {formErrors.brief.length > 0 && (
                                    <span className="errorMessage">{formErrors.userName}</span>
                                )}
                            </div>
                            <div className="form-lower-section">
                                <button type="submit">Create Campaign</button>
                            </div>
                        </form>
                    </div>
                </div>
            </React.Fragment>


        )
    }
}
export default CampaignForm;