import React, { Component } from 'react';
import { connect } from 'react-redux';

import Select from '../../filterform/select/Select'


class SelectListDropDown extends Component {

    renderAssignButton() {
        if (this.props.userCredentials === "") {
            return null;
        } else {
            let listDataArray = [];
            const { influencersGroup } = this.props;
            if (influencersGroup !== null) {
                influencersGroup.groups.map(key => {
                    let listDataObject = {
                        "value": key.fields.list_name
                    }
                    listDataArray.push(listDataObject);
                })
            }
            return <Select
                id="assignLists"
                class="assignButton col"
                belongsTo="assignButton"
                placeholder="Select Lists"
                options={listDataArray}
                multiple />
        }
    }

    render() {
        return (
            this.renderAssignButton()
        );
    }
}

const mapStateToProps = state => ({
    selectedInfluencersData: state.selectedInfluencersData,
    influencersGroup: state.influencersGroup,
    userCredentials: state.userCredentials
});


export default connect(mapStateToProps)(SelectListDropDown);
