import React, { Component } from 'react';
import { connect } from 'react-redux';

import { addInfluencersGroup, storeListNames } from "../../../actions";
import './ConfirmCreationModal.css';

class ConfirmCreationModal extends Component {

    componentDidMount() {
        let errorMessage = document.getElementById("errorMessageList");
        errorMessage.style.display = "none";
    }
    checkIfListNameExists(listName) {
        console.log(this.props.listNamesArray);
        let alreadyNamedLists = this.props.listNamesArray;
        if (alreadyNamedLists) {
            if (alreadyNamedLists.includes(listName)) {
                console.log("present");
                return false
            } else {
                console.log('no present');
                let updatedNameLists = alreadyNamedLists.push(listName);
                this.props.storeListNames(updatedNameLists);
                return true;
            }
        }

    }
    addNewInfluencersGroup() {
        let listName = document.getElementById("listName").value;
        if ((listName !== "") && (this.checkIfListNameExists(listName))) {
            const userListInfo = {
                userCredentials: this.props.userCredentials,
                listName: listName
            }
            this.props.addInfluencersGroup(userListInfo);
            document.getElementById("listName").value = "";
            window.$("#confirmCreation").modal("toggle");
            let body = document.getElementsByTagName("body");
            body[0].classList.remove("modal-open");
        } else {
            let errorMessage = document.getElementById("errorMessageList");
            errorMessage.style.display = "block";
        }
    }
    render() {
        return (
            <div className="modal fade" id="confirmCreation" tabIndex="-1" role="dialog" data-backdrop="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Enter the name for your new list</p>
                            <div className="CreationModal">
                                <input type="text" id="listName" placeholder="Enter List Name" />
                                <span id="errorMessageList" className="errorMessage">Required field And Must be Unique</span>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" id="addNewList" className="btn btn-primary" onClick={() => { this.addNewInfluencersGroup() }}>Yes</button>
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    userCredentials: state.userCredentials,
    listNamesArray: state.listNamesArray
});

const mapDispatchToProps = dispatch => {
    return {
        addInfluencersGroup: userCredentials => {
            dispatch(addInfluencersGroup(userCredentials));
        },
        storeListNames: newListName => {
            dispatch(storeListNames(newListName));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmCreationModal);