import { combineReducers } from "redux";
import isDataLoading from "./isDataLoading";
import influencers from "./influencers";
import influencersGroup from "./influencersGroup";
import filtersMenu from './filtersMenu';
import isAuthenticated from './isAuthenticated';
import campaignsGroup from './campaignsGroup';
import userCredentials from './userCredentials';
import listNamesArray from './listNamesArray';
import selectedInfluencersData from './selectedInfluencersData';
import isReportGenerated from './isReportGenerated';
import listWhoseReportGenerated from './listWhoseReportGenerated';

export default combineReducers({
  isDataLoading,
  influencers,
  filtersMenu,
  influencersGroup,
  listWhoseReportGenerated,
  isAuthenticated,
  listNamesArray,
  isReportGenerated,
  userCredentials,
  selectedInfluencersData,
  campaignsGroup
});
