import React, { Component } from 'react';
import { connect } from 'react-redux';

import { setSelectedInfluencersData } from '../../../../actions';

class DeselectAllInfluencersButton extends Component {
    deselectAllInfluencers = () => {
        let selectedInluencersId = "";
        const allCardCheckboxes = document.getElementsByClassName("influencer-card-checkbox");
        for (let i = 0; i < allCardCheckboxes.length; i++) {
            if (allCardCheckboxes[i].checked) {
                allCardCheckboxes[i].checked = false;
            }
        }
        this.props.setSelectedInfluencersData(selectedInluencersId);
    }
    render() {
        return (
            <button className="button-style col" onClick={this.deselectAllInfluencers}>Deselect All</button>
        );
    }
}
const mapDispatchToProps = dispatch => {
    return {
        setSelectedInfluencersData: selectedInfluencersData => {
            dispatch(setSelectedInfluencersData(selectedInfluencersData));
        }
    }
}


export default connect(null, mapDispatchToProps)(DeselectAllInfluencersButton);