import React, { Component } from "react";
import { connect } from "react-redux";

import Loader from "../loader/Loader";
import Header from '../header/Header'
import ListCampaignButton from "../utilities/listcamapignbutton/ListCampaignButton";
import ListCampaignContent from "../utilities/listcampaigncontent/ListCampaignContent";
import DownloadReportModal from './DownloadReportModal';

import { fetchInfluencerGroupsData, storeListNames } from "../../actions";
import "./ShortListPage.css";
import GeneratingReportModal from "./measureperformanceModal/GeneratingReportModal";

class ShortListPage extends Component {

  componentDidMount() {
    this.props.fetchInfluencerGroupsData(this.props.userCredentials);
  }
  renderDownLoadButtonModal() {
    if (this.props.isReportGenerated) {
      // document.getElementById("Loader").classList.remove("show");
      // window.$(".modal-backdrop").hide();
      window.$("#Loader").modal("hide");
      window.$("#downloadReport").modal("show");
    }
  }
  renderButtons() {
    let listNamesArray = [];
    if (this.props.influencersGroup.groups) {
      var listButtons =
        this.props.influencersGroup.groups.map((key, index) => {
          listNamesArray.push(key.fields.list_name);
          return (
            <ListCampaignButton key={index} data={index + 1} ListName={key.fields.list_name} type="List" />
          );
        })
      this.props.storeListNames(listNamesArray);
      return listButtons;
    } else {
      return null
    }
  }

  renderContent() {
    if (this.props.influencersGroup.groups) {
      var listContents =
        this.props.influencersGroup.groups.map((key, index) => {
          return (
            <ListCampaignContent
              key={index}
              id={index + 1}
              data={key.fields.influencers}
              listName={key.fields.list_name}
              type="List"
            />
          );
        })
      return listContents;
    } else {
      return null
    }
  }
  renderList() {
    if (!this.props.isDataLoading) {
      return (
        <div className="row" id="container">
          <div className="tab col-sm-3" id="listcont">
            <button className="topbutton mt-0">Select List</button>
            {this.renderButtons()}
            <ListCampaignButton data="new" type="List" />
            <ListCampaignButton data="deleteAll" type="List" />
            <ListCampaignButton data="performance" type="List" />
          </div>
          <div className="col-sm-9 main-content" id="list-content">
            {this.renderContent()}
          </div>
        </div>
      );
    } else {
      return <Loader />;
    }
  }
  render() {
    return (
      <React.Fragment>
        <Header />
        <hr />
        {this.renderList()}
        {this.renderDownLoadButtonModal()}
        <DownloadReportModal />
        <GeneratingReportModal />
      </React.Fragment>

    );
  }
}

const mapStateToProps = state => ({
  influencersGroup: state.influencersGroup,
  isDataLoading: state.isDataLoading,
  userCredentials: state.userCredentials,
  isReportGenerated: state.isReportGenerated
});

const mapDispatchToProps = dispatch => {
  return {
    fetchInfluencerGroupsData: userCredentials => {
      dispatch(fetchInfluencerGroupsData(userCredentials));
    },
    storeListNames: newListName => {
      dispatch(storeListNames(newListName));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ShortListPage);
