import React, { Component } from 'react';
import { connect } from 'react-redux';

import { deleteInfluencersGroup, storeListNames } from "../../actions";

class ConfirmDeletionListModal extends Component {

    deleteInfluencersGroup(list_selected, deleteModalId) {
        window.$("#" + deleteModalId).modal("toggle");
        console.log("insde delete func ", deleteModalId);
        const groupUserInfo = {
            listSelected: list_selected,
            userCredentials: this.props.userCredentials
        }
        console.log(groupUserInfo);
        this.props.deleteInfluencersGroup(groupUserInfo);
        let alreadyExistsListNames = this.props.listNamesArray;
        const indexToRemove = alreadyExistsListNames.indexOf(list_selected);
        alreadyExistsListNames = [...alreadyExistsListNames.slice(0, indexToRemove), ...alreadyExistsListNames.slice(indexToRemove + 1)];
        this.props.storeListNames(alreadyExistsListNames);
    }
    renderDialogbox(listName) {
        if (listName === "") {
            return null;
        } else {
            let deleteModalId = "confirmDeletionFor" + this.props.listName;
            return (
                <div className="modal fade" id={deleteModalId} tabIndex="-1" role="dialog" data-backdrop="false">
                    <div className="modal-dialog modal-dialog-centered" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <p>Are you sure you want to delete this lists</p>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-primary" onClick={() => { this.deleteInfluencersGroup(this.props.actualListName, deleteModalId) }}>Yes</button>
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
    }
    render() {
        return this.renderDialogbox(this.props.listName)
    }
}

const mapStateToProps = state => ({
    userCredentials: state.userCredentials,
    listNamesArray: state.listNamesArray
});

const mapDispatchToProps = dispatch => {
    return {
        deleteInfluencersGroup: groupUserInfo => {
            dispatch(deleteInfluencersGroup(groupUserInfo));
        },
        storeListNames: newListName => {
            dispatch(storeListNames(newListName));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmDeletionListModal);