import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import onClickOutside from 'react-onclickoutside';
import { compose } from 'redux';

import './DropDownOnClick.css';

import ChevronDown from '../ChevronDown';
import ChevronUp from '../ChevronUp';

import { fetchInfluencerData, setIsAuthenticated } from '../../../actions';
import { getFilter } from '../../../getFilterFormValues';


class DropDownOnClick extends Component {
    state = {
        ...this.props,
        classname: this.props.classname,
        items: this.props.items || [],
        selectedItem: this.props.items[0] || this.props.selectedItem,
        belongTo: this.props.belongTo,
        showItems: false,
    }
    dropDown() {
        this.setState(prevState => ({
            showItems: !prevState.showItems
        }))
    }
    selectedItem(item) {
        if (item.id === "login") {
            this.props.setIsAuthenticated(false);
        }
        this.setState(() => ({
            selectedItem: item,
            showItems: false,
        }))
    }
    setOrder = (item_id) => {
        document.getElementById("order-by").value = item_id;
        const filter = getFilter();
        const url = `https://secure-bastion-20455.herokuapp.com/influencer_listings/search?${filter.categories}&${filter.locations}&minfollowers=${filter.minfollowers}&maxfollowers=${filter.maxfollowers}${filter.picture_engagement}${filter.video_engagement}${filter.orderby}`;
        this.props.fetchInfluencerData(url);
    }
    onClick = (item) => {
        this.selectedItem(item);
        this.setOrder(item.id);
    }

    handleClickOutside = () => {
        if (this.state.showItems) {
            var dropdownlist = document.getElementsByClassName("select-box--items");
            for (let i = 0; i < dropdownlist.length; i++) {
                dropdownlist[i].style.display = "none";
            }
            this.dropDown();
        }
    }


    renderAccordingly() {
        var list = null;
        if (this.state.belongTo === "Header") {
            list = this.state.items.map((item, index) => {
                return (
                    <Link to={`/${item.id}`} key={index}>
                        <div key={item.id}
                            onClick={() => { this.selectedItem(item) }}
                            className={this.state.selectedItem === item ? 'selected' : ''}>
                            {item.value}
                        </div>
                    </Link>)
            })
        } else {
            list = this.state.items.map(item => {
                return (
                    <div key={item.id}
                        onClick={this.onClick.bind(this, item)}
                        className={this.state.selectedItem === item ? 'selected' : ''}>
                        {item.value}
                    </div>
                )
            }
            )
        }
        return list;
    }

    render() {
        return (
            <div className={this.state.belongTo === "Header" ? "headerdropdowncontainer" : "orderbydropdowncontainer"}>
                <div className={this.state.classname} onClick={() => { this.dropDown() }}>
                    <div className="select-box--container" >
                        <div className="select-box--selected-item" >
                            {this.state.selectedItem.value}
                        </div>
                        <div className="select-box--arrow" >
                            <span>{this.state.showItems ? <ChevronUp /> : <ChevronDown />}
                            </span>
                        </div>
                    </div>
                    <div ref={node => this.node = node} className="select-box--items"
                        style={{ display: this.state.showItems ? 'block' : 'none' }}
                    >
                        {this.renderAccordingly()}
                    </div>
                </div>
                <input type="hidden" id={this.state.belongTo} value={this.state.selectedItem.id}
                />

            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        fetchInfluencerData: url => {
            dispatch(fetchInfluencerData(url));
        },
        setIsAuthenticated: isAuthenticated => {
            dispatch(setIsAuthenticated(isAuthenticated));
        }
    };
};

export default compose(connect(null, mapDispatchToProps), onClickOutside)(DropDownOnClick);
